import { Component, OnInit, ViewEncapsulation, Input } from '@angular/core';
import { Car } from '../cars/car';
import { Observable } from 'rxjs/Rx';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-car-details',
  templateUrl: './car-details.component.html',
  styleUrls: ['./car-details.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class CarDetailsComponent implements OnInit {
  @Input() car: Car;
  private mainUrl = 'http://localhost:8080/garage';
  newStatus: string;

  constructor(private http: HttpClient,
    private route: ActivatedRoute,
    private location: Location,
    private router: Router) { }

  ngOnInit() {
    console.log('in Init!');
    this.getCar();
  }

  getCar(): void {
  const id = +this.route.snapshot.paramMap.get('id');
  const url = `${this.mainUrl}/car/${id}`;
  this.http.get<Car>(url).subscribe(data => {
          console.log('http jaaaaaa');
          this.car = data;
          console.log("I CANT SEE DATA HERE: ", this.car);
        })
}
  nextStatus(status:string): void{
      const id = +this.route.snapshot.paramMap.get('id');
      const url = `${this.mainUrl}/update/${id}`;
      if (status == null || status == 'waiting'){
        console.log(this.newStatus);
        this.newStatus = 'processing';
      }
      else if (status == 'processing'){
        this.newStatus = 'done';
        console.log(this.newStatus);
      }
      else{
        console.log('status done already');
      }

      this.http.put<Car>(url, this.newStatus, {
        headers: new HttpHeaders().set('Content-Type', 'text/plain'),
      }).subscribe(data => {
             console.log(data);
              this.router.navigate(['']);
            })
  }

}



